<template>
  <div class="page-tabbar">

    <!--header‰ΩçÁΩÆ-->
    <bheader v-bind:title="selected"></bheader>
    <div class="page-wrap">
      <!--ÊØè‰∏Ä‰∏™ÊéßÂà∂Âô®ÂèØËá™ÂÆö‰πâ-->
      <mt-tab-container class="page-tabbar-container" v-model="selected">
        <mt-tab-container-item :id="item.id" v-for="item in tabList" :key="item.id">
          <!--Ëá™ÂÆö‰πâÁªÑ‰ª∂ÁªìÂêà-->
          <mt-cell v-for="n in 90" :title="n + '‰∫ã‰∏ö‰ªªÂä°'" :key="n" v-if="item.id == '‰∫ã‰∏ö'" />
              <news-selected-bar v-if="item.id == 'ËµÑËÆØ'"></news-selected-bar>
          <resource-vue v-if="item.id == 'ËµÑÊ∫ê'"></resource-vue>
          <mt-cell v-for="n in 100" :title="n + 'ÊàëÁöÑÈ°µÈù¢cell'" :key="n" v-if="item.id == 'ÊàëÁöÑ'" />
        </mt-tab-container-item>       
      </mt-tab-container>
    </div>
    <mt-tabbar v-model="selected" :fixed="true">
      <mt-tab-item :id="list.id" v-for="list in tabList" :key="list.id">
        <img slot="icon" :src="list.img">
        <span>{{list.id}}</span>
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
import newsSelectedBar from "./NewsSelectedBar";
import resourceVue from "./Resource"
export default {
  name: "AppTabbar",
  created() {},
  data() {
    return {
      selected: this.defines.tabBarItemSelected,

      tabList: [
        {
          id: "‰∫ã‰∏ö",
          img: "static/100x100.png"
          // img: require('../assets/100x100.png')
        },
        {
          id: "ËµÑËÆØ",
          img: "static/100x100.png"
        },
        {
          id: "ËµÑÊ∫ê",
          img: "static/100x100.png"
        },
        {
          id: "ÊàëÁöÑ",
          img: "static/100x100.png"
        }
      ]
    };
  },
  components: {
    newsSelectedBar: newsSelectedBar,
    resourceVue: resourceVue
  },
  watch: {
    selected: function(newValue, oldValue) {
      // alert(this.tabBarItemSelected)
      this.defines.settabBarItemSelected(newValue);
      console.log("newValue, oldValue", newValue, oldValue);
    }
  }
};
</script>

<style scoped>
.page-tabbar {
  overflow: hidden;
  height: 100vh;
}
.page-wrap {
  overflow: auto;
  height: 100%;
  padding-bottom: 100px;
}
</style>



