      <button @click.native="sheetVisible=true" size="large">actionsheet0,Â∏¶ÂèñÊ∂àÊåâÈíÆ, Â•ΩÂÉèÂ§±Ë¥•</button>
      <button @click.native="sheetVisible2=true" size="large">actionsheet1,‰∏çÂ∏¶ÂèñÊ∂àÊåâÈíÆ, Â•ΩÂÉèÂ§±Ë¥•</button>

