    <button @click="notFound">ËøõÂÖ•404È°µÈù¢</button>

    <div>
    <input placeholder="ËæìÂÖ•ÊµãËØïÊï∞ÊçÆ" v-model="text">
    {{text}}
    <button @click="chan√•geMessage">‰øÆÊîπÊñáÂ≠ó</button>
    </div>
   
