      <button @click="openNativeMethodWithParam()">ÊâìÂºÄÂéüÁîüÁïåÈù¢ÁöÑÊñπÊ≥ïÔºåÂ∏¶ÂèÇÊï∞</button>
      <button @click="openNativeMethodWithParams()">ÊâìÂºÄÂéüÁîüÁïåÈù¢ÁöÑÊñπÊ≥ïÔºåÂ∏¶Â§ö‰∏™ÂèÇÊï∞</button>
    </div>
  </div>
