  created() {
    // alert('ÂºÄÂßãÂàõÂª∫viewMain')
  },
  watch: {
    text: function(newV, oldV) {
      console.log(newV);
    }
  },
  data() {
    return {
      text: "ÂÜÖÂÆπ",
      sheetVisible: false,
      sheetVisible2: false,
      actions: [],
      actions2: [],
      price: 0,
      num: 0,
