    },
    indicator0: function() {
      Indicator.open("Êï∞ÊçÆÂä†ËΩΩ‰∏≠...");
    },
    indicator1: function() {
      Indicator.open({
        text: "Loading",
        spinnerType: "fading-circle"
      });
      closeIndicator();
    },
    closeIndicator: function() {
