        title: "Notice",
        message: "Are you sure?",
        showCancelButton: true
      });
    },
    messageBox1: function() {
      MessageBox.prompt("ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÂßìÂêç").then(({ value, action }) => {
        Toast(value);
      });
    },
    confirm: function() {
      MessageBox.confirm("Ê∂àÊÅØ", "title").then(action => {
        Toast("ÊèêÁ§∫");
      });
    },
    takePhoto: function() {
      console.log("taking photo");
    },
    openAlbum: function() {
      console.log("opening album");
    },
    goBack: function() {
      history.go(-1);
    },
    showAction: function() {
