    },
    openTabbar: function() {
      this.$router.push({ name: "appTabbar" });
    },
    testParams() {
      this.$router.push({
        // ‰º†ÈÄíÂèÇÊï∞
        name: "newsDetail",
        query: { id: 1, name: "query2" },
        params: { id: 2, name: "params2" }
      });
    },
    notFound() {
      this.$router.push({ name: "home" });
    },
    openNativeMethod: function() {
      try {
       vueProject.test();
      } catch (error) {
        Toast(error.message)
      }
    },
    openNativeMethodWithParam () {
        try {
          vueProject.testFunction('ËøôÊòØ‰∏Ä‰∏™ÊµãËØïÊï∞ÊçÆÔºåÂ∏åÊúõ‰Ω†ËÉΩÊ≠£Â∏∏Êé•Êî∂ - Êù•Ëá™Â§ñÂ§™Á©∫')
        } catch (error) {
        Toast(error.message)
        }
    }, 
    openNativeMethodWithParams () {
      try {
        vueProject.testFunctionWith('ÂèÇÊï∞1','ÂèÇÊï∞‰∫å','‰∏çÁü•ÈÅìËøôÊ†∑ËÉΩ‰∏çËÉΩËøáÂéª - Êù•Ëá™Â§ñÂ§™Á©∫ÁöÑÂëºÂî§')
      } catch (error) {
