      }
    }
  },
  // ËÆ°ÁÆóÂ±ûÊÄß
  computed: {
    sum() {
      return this.price * this.num * this.rate;
    }
  },
  mounted() {
    this.actions = [
      {
        name: "ÊãçÁÖß",
        method: this.takePhoto
      },
      {
        name: "Áõ∏ÂÜåÈÄâÂèñÂõæÁâá",
        method: this.openAlbum
      }
