  },
  //  Êé•Âèó‰ºèÁªÑ‰ª∂ÂèÇÊï∞ÂÄº
  props: ["textOne", "textTwo", "type"]
};
</script>

<style scoped>
.middle {
  margin-top: 10px;
  width: 90%;
  height: 100%;
  /*display: flex;*/
  align-items: center;
  justify-content: center;
  position: absolute;
  background-color: aliceblue;
}
.webViewObject {
  width: 200px;
  height: 100px;
  background-color: brown;
}
</style>



